.hero-height(@ratio){
  .hero {
    width: 100%;
    padding-top: 100% / @ratio;
  }
}

.site-header {
  .hero {
    min-height: 200px;
    background-color: #ccc;
    background-size: cover;
    background-position: center center;
    position: relative;

    h1,
    h2 {
      margin-bottom: 0;
    }

    h2 {
      margin-top: 0;
    }

    .post-title {
      position: absolute;
      bottom: 0.6em;
      left: 0.6em;
      margin: 20em 0 0;
      h1,
      h2,
      h3 {
        span {
          display: inline-block;
          background: @color-background-light;
          margin: .2em 0 0;
          line-height: 1.5;
          padding: 0 .2em;
        }
      }
    }

  }

  figcaption {
    font-size: 0.75rem;
    text-align: right;
    p {
      margin: 0;
    }
    a {
      padding: 0;
    }
  }

}

.hero-height(2);

@media (min-aspect-ratio: ~"4/3"){
  .hero-height(2.667);
}

@media (max-aspect-ratio: ~"3/4"){
  .hero-height(1.667);
}
